.litegraph-editor {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;

  background-color: #333;
  color: #eee;
  font: 14px Tahoma;

  position: relative;
}

.litegraph-editor h1 {
  font-family: "Metro Light", Tahoma;
  color: #ddd;
  font-size: 28px;
  padding-left: 10px;
  /*text-shadow: 0 1px 1px #333, 0 -1px 1px #777;*/
  margin: 0;
  font-weight: normal;
}

.litegraph-editor h1 span {
  font-family: "Arial";
  font-size: 14px;
  font-weight: normal;
  color: #aaa;
}

.litegraph-editor h2 {
  font-family: "Metro Light";
  padding: 5px;
  margin-left: 10px;
}

.litegraph-editor * {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.litegraph-editor .content {
  position: relative;
  width: 100%;
  height: calc(100% - 85px);
  background-color: #1a1a1a;
}

.litegraph-editor .header {
  position: relative;
  height: 40px;
  background-color: #333;
  /*border-radius: 10px 10px 0 0;*/
}

.litegraph-editor .footer {
  position: relative;
  height: 45px;
  background-color: #333;
  /*border-radius: 10px 10px 0 0;*/
}

.litegraph-editor .tools,
.litegraph-editor .tools-left,
.litegraph-editor .tools-right {
  position: absolute;
  top: 2px;
  right: 0px;
  vertical-align: top;

  margin: 2px 5px 0 0px;
}

.litegraph-editor .tools-left {
  right: auto;
  left: 4px;
}

.litegraph-editor .footer .tools-right form {
  display: inline;
}

.litegraph-editor .miniwindow {
  background-color: #333;
  border: 1px solid #111;
}

.litegraph-editor .miniwindow .corner-button {
  position: absolute;
  top: 2px;
  right: 2px;
  font-family: "Tahoma";
  font-size: 14px;
  color: #aaa;
  cursor: pointer;
}

/* BUTTONS **********************/

.litegraph-editor .btn {
  /*font-family: "Metro Light";*/
  color: #ccc;
  font-size: 20px;
  min-width: 30px;
  /*border-radius: 0.3em;*/
  border: 0 solid #666;
  background-color: #3f3f3f;
  /*box-shadow: 0 0 3px black;*/
  padding: 4px 10px;
  cursor: pointer;
  transition: all 1s;
  -moz-transition: all 1s;
  -webkit-transition: all 0.4s;
}

.litegraph-editor button:hover {
  background-color: #999;
  color: #fff;
  transition: all 1s;
  -moz-transition: all 1s;
  -webkit-transition: all 0.4s;
}

.litegraph-editor .footer .tools-right button {
  color: #fff;
  background-color: #dc3545;
  border-color: #dc3545;
}

.litegraph-editor .footer .tools-right button:hover {
  color: #fff;
  background-color: #c82333;
  border-color: #bd2130;
  transition: all 1s;
  -moz-transition: all 1s;
  -webkit-transition: all 0.4s;
}

.litegraph-editor button:active {
  background-color: white;
}

.litegraph-editor button.fixed {
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 1.2em;
}

.litegraph-editor button img {
  margin: -4px;
  vertical-align: top;
  opacity: 0.8;
  transition: all 1s;
}

.litegraph-editor button:hover img {
  opacity: 1;
}

.litegraph-editor .header button {
  height: 32px;
  vertical-align: top;
}

.litegraph-editor .footer button {
  /*font-size: 16px;*/
}

.litegraph-editor .toolbar-widget {
  display: inline-block;
}

.litegraph-editor .editor-area {
  width: 100%;
  height: 100%;
}

/* METER *********************/

.litegraph-editor .loadmeter {
  font-family: "Tahoma";
  color: #aaa;
  font-size: 12px;
  border-radius: 2px;
  width: 130px;
  vertical-align: top;
}

.litegraph-editor .strong {
  vertical-align: top;
  padding: 3px;
  width: 30px;
  display: inline-block;
  line-height: 8px;
}

.litegraph-editor .cpuload .bgload,
.litegraph-editor .gpuload .bgload {
  display: inline-block;
  width: 90px;
  height: 15px;
  background-image: url("/imgs/load-progress-empty.png");
}

.litegraph-editor .cpuload .fgload,
.litegraph-editor .gpuload .fgload {
  display: inline-block;
  width: 4px;
  height: 15px;
  max-width: 90px;
  background-image: url("/imgs/load-progress-full.png");
}

.litegraph-editor textarea.code,
.litegraph-editor div.code {
  height: 100%;
  width: 100%;
  background-color: black;
  padding: 4px;
  font: 16px monospace;
  overflow: auto;
  resize: none;
  outline: none;
}

.litegraph-editor .codeflask {
  background-color: #2a2a2a;
}

.litegraph-editor .codeflask textarea {
  opacity: 0;
}
